<template>
  <form
    action="https://formsubmit.co/tomodachibcn@gmail.com"
    method="POST"
    class="row resevation-form"
  >
    <input type="hidden" name="_captcha" value="false" />
    <div class="col-12 col-sm-6">
      <label for="name" class="">Name*</label>
      <input
        type="text"
        class="form-control"
        id="name"
        name="name"
        required
        placeholder="Name"
      />
    </div>
    <div class="col-12 col-sm-6">
      <label for="phone" class="">Contact phone*</label>
      <input
        type="tel"
        class="form-control"
        id="phone"
        name="phone"
        required
        pattern="[0-9]*"
        placeholder="Phone"
      />
    </div>
    <div class="col-12 col-sm-6">
      <label for="exampleInputEmail1" class="">Email address*</label>
      <input
        type="email"
        class="form-control"
        id="exampleInputEmail1"
        aria-describedby="emailHelp"
        name="email"
        placeholder="Email"
        required
      />
    </div>
    <div class="col-12 col-sm-6">
      <label for="exampleInputEmail1" class="">Date</label>
      <input
        type="date"
        class="form-control"
        id="date"
        name="date"
        :min="minDate"
        v-model="selectedDate"
        @input="validateDate"
      />
      <p v-if="disabledDateSelected" class="text-danger">
        We're closed on Tuesdays. Please select another date. See you soon!
      </p>
    </div>
    <div class="col-12 col-sm-6 mb-4">
      <label for="exampleInputEmail1" class="">Time</label>
      <select id="timeSelect" class="d-block" required name="time">
        <option disabled value="">Select a Time</option>
        <option
          v-for="(time, index) in availableBookingTime"
          :key="index"
          :value="timeOption"
        >
          {{ time }}
        </option>
      </select>
    </div>
    <textarea
      rows="8"
      class="field mb-4 font-bold"
      name="message"
      placeholder="Write you message here"
    ></textarea>
    <button type="submit" class="btn btn-primary col-6 m-auto submit-btn">
      Submit
    </button>
  </form>
</template>

<script>
export default {
  name: "ContactForm",
  data: function () {
    return {
      selectedTime: "13:00",
      selectedDate: "",
      disabledDateSelected: false,
    };
  },
  computed: {
    availableBookingTime() {
      return [
        "13:00",
        "13:30",
        "14:00",
        "14:30",
        "15:00",
        "15:30",
        "19:00",
        "19:30",
        "20:00",
        "20:30",
        "20:30",
        "21:30",
        "22:00",
        "22:30",
        "22:30",
      ];
    },
    minDate() {
      const today = new Date().toISOString().split("T")[0];
      return today;
    },
  },
  methods: {
    validateDate() {
      this.disabledDateSelected = false;
      const date = new Date(this.selectedDate);
      if (date.getDay() === 2) {
        this.selectedDate = ""; // Clear the input
        this.disabledDateSelected = true;
      }
    },
  },
};
</script>

